@mixin containerComponent {

	.container {
		@extend .full-width;
		@extend .x-centered;
		@extend .padding-left-1;
		@extend .padding-right-1;

		&-inner {
			@extend .full-width;
			@extend .x-centered;
			max-width: #{$largeBreakpoint}px;

			&-xl {
				@extend .container-inner;
				max-width: #{$extraLargeBreakpoint}px; } } } }
